import React from 'react'

import { useForm } from 'react-hook-form'

export const FormularioUsuarios = (props) => {

    const { register, errors, handleSubmit } = useForm();


    const onSubmit = (data, e) => {
        props.AgregarUsuario(data)
        e.target.reset()
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>

            <h4 className="text-secondary">Agregar usuario</h4>

            <label htmlFor="">Nombre</label>
            <input
                className="form-control"
                type="text"
                name="nombre"
                autoComplete="off"
                ref={
                    register({
                        required: { value: true, message: 'Campo requerido' }
                    })
                } />
            <div>
                <span className="text-danger">{errors?.nombre?.message}</span>
            </div>

            <label htmlFor="">Usuario</label>
            <input
                type="text"
                className="form-control"
                name="usuario"
                autoComplete="off"
                ref={
                    register({
                        required: { value: true, message: 'Campo requerido' }
                    })
                } />
            <div>
                <span className="text-danger">{errors?.usuario?.message}</span>
            </div>
            <br />
            <button type="submit" className="btn btn-info btn-block">Agregar Usuario</button>
        </form>
    )
}
