import React from 'react'

export const TablaUsuarios = (props) => {

    console.log(props)

    return (
        <table className="table">
            <thead>
                <tr>
                    <th>Nombre</th>
                    <th>Usuario</th>
                    <th>Acciones</th>
                </tr>
            </thead>
            <tbody>
                {

                    props.usuarios.map((data, i) => {
                        return (
                            <tr key={data.id}>
                                <td>{data.nombre}</td>
                                <td>{data.usuario}</td>
                                <td>
                                    <div className="d-flex justify-content-between">
                                        <button
                                            className="btn btn-info btn-sm"
                                            onClick={() => {
                                                props.EditarUsuario(data)
                                            }}>
                                            Editar
                                        </button>
                                        <button
                                            className="btn btn-danger btn-sm"
                                            onClick={() => {
                                                props.EliminarUsuario(data.id)
                                            }}>
                                            Borrar
                                        </button>
                                    </div>
                                </td>
                            </tr>
                        )
                    })


                }
            </tbody>
        </table>
    )
}
