
import React, { useState } from 'react'
import { v4 as uuidv4 } from 'uuid'


import { TablaUsuarios } from "./Componentes/TablaUsuarios";
import { FormularioUsuarios } from './Componentes/FormularioUsuarios';
import { FormularioEditar } from './Componentes/FormularioEditar';

import './Estilos/App.css'

const App = () => {

  const datosUsuarios = [
    { id: uuidv4(), nombre: "Pedro Navajas", usuario: "Pedro123" },
    { id: uuidv4(), nombre: "Camilo Cifuentes", usuario: "Camilo123" },
    { id: uuidv4(), nombre: "Marcela CastaÃ±eda", usuario: "Marcela123" }
  ]

  const [usuarios, setUsuarios] = useState(datosUsuarios)
  const [editando, setEditando] = useState(false)
  const [usuarioActual, setUsuarioActual] = useState({ id: null, nombre: '', usuario: '' })

  const AgregarUsuario = (usuario) => {
    usuario.id = uuidv4()
    setUsuarios([
      ...usuarios, usuario
    ])
  }

  const EditarUsuario = (user) => {
    console.log(user)
    setEditando(true)

    setUsuarioActual({
      id: user.id,
      nombre: user.nombre,
      usuario: user.usuario
    })

  }

  const ActualizarUsuario = (id, usuarioAct) => {
    setEditando(false)
    setUsuarios(usuarios.map(user => (user.id === id ? usuarioAct : user)))

  }

  const EliminarUsuario = (id) => {
    //console.log(id)
    let filtroArray = usuarios.filter(data => data.id !== id)
    setUsuarios(filtroArray)
  }


  return (
    <div className="container-fluid p-4">

      <div className="row">

        <div className="col-md-6">
          {
            editando ? (
              <FormularioEditar
                usuarioActual={usuarioActual}
                ActualizarUsuario={ActualizarUsuario} />
            ) : (
                <FormularioUsuarios AgregarUsuario={AgregarUsuario} />
              )
          }
        </div>

        <div className="col-md-6 table-responsive-sm">
          <TablaUsuarios
            usuarios={usuarios}
            EliminarUsuario={EliminarUsuario}
            EditarUsuario={EditarUsuario}
          />
        </div>

      </div>

    </div>
  );
}

export default App;
