import React from 'react'

import { useForm } from 'react-hook-form'

export const FormularioEditar = (props) => {

    const { usuarioActual } = props

    const { register, errors, handleSubmit, setValue } = useForm({
        defaultValues: usuarioActual
    });

    setValue('nombre', usuarioActual.nombre)
    setValue('usuario', usuarioActual.usuario)

    const onSubmit = (data, e) => {
        console.log(data)
        data.id = usuarioActual.id
        
        props.ActualizarUsuario(usuarioActual.id, data)
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>

            <h4 className="text-secondary">Editar usuario</h4>
            <label htmlFor="">Nombre</label>
            <input
                className="form-control"
                type="text"
                name="nombre"
                autoComplete="off"
                ref={
                    register({
                        required: { value: true, message: 'Campo requerido' }
                    })
                } />
            <div>
                <span className="text-danger">{errors?.nombre?.message}</span>
            </div>

            <label htmlFor="">Usuario</label>
            <input
                type="text"
                className="form-control"
                name="usuario"
                autoComplete="off"
                ref={
                    register({
                        required: { value: true, message: 'Campo requerido' }
                    })
                } />
            <div>
                <span className="text-danger">{errors?.usuario?.message}</span>
            </div>
            <br />
            <button
                type="submit"
                className="btn btn-info btn-block"
            >Editar Usuario</button>
        </form>
    )
}
